body {
  margin: 0;
  background-color: #fff;
  font-family: "Golos-Text", sans-serif;
  font-size: 15px;
  line-height: 1.48;
  font-weight: 400;
  color: #404040;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #000;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #000;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Components */
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 82px;
}
.header__logo {
  display: flex;
  width: 100%;
  max-width: 520px;
  margin-right: 24px;
}
.header__logo-img {
  margin-left: 1px;
  margin-right: 24px;
}
.header__logo-text {
  font-size: 11px;
  line-height: 1.58;
  color: #8F9090;
}

.logo {
  height: 32px;
}

.actions-menu {
  display: flex;
  align-items: center;
}
.actions-menu__list {
  display: flex;
}
.actions-menu__link {
  margin-right: 32px;
  font-size: 14px;
  line-height: 1.58;
  color: #003154;
}
.actions-menu__link--feedback {
  padding-left: 23px;
  position: relative;
}
.actions-menu__link--feedback:before {
  content: "";
  display: block;
  width: 17px;
  height: 20px;
  margin-top: -1px;
  background-image: url("../images/letter-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 0;
  left: 0;
}
.actions-menu__search {
  padding: 0;
  border: 0;
  background: none;
}
.actions-menu__search:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-image: url("../images/search-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.site-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 54px;
}
.site-nav__link {
  padding-bottom: 7px;
  padding-top: 7px;
  font-size: 15px;
}
.site-nav__link--subnav {
  padding-right: 15px;
  position: relative;
}
.site-nav__link--subnav:after {
  content: "";
  display: block;
  width: 8px;
  height: 4px;
  margin-top: 1px;
  background-image: url("../images/arrow-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 1;
}